import React, { Component } from 'react'
import axios from 'axios';
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as examAction  from "../../../../actions/examAction";
import { toastSuccess } from '../../../../helpers/toastHelper';
class AdminAddItemExam extends Component {
    constructor(props) {//khi to gi tr
        super(props)
        this.state = {
          _id:'',
          examName: '',
          // examEasyNumber:'',
          // examMediumNumber : '',
          // examDifficultNumber : '',
          examTimeMake : '05 pht',
          classId : 'Ton lp 1',
          examCategoryNumber : 'Ton lp 1',
          errors: {},
          classObject: [],
        }
        this.onChange = this.onChange.bind(this)// n hiu this  y l Resgister
        this.onSubmit = this.onSubmit.bind(this)
      }
      componentDidMount() {
        axios.get('https://cititechnodejs.herokuapp.com/admin/classsubject/list/')
        .then(response => {
         this.setState({classObject:response.data})
          })
           .catch(function (error){
              console.log(error +"loi ne");
          })
      }
      renderClass = () =>{
        let{classObject}=this.state;
        console.log(classObject);
        return ( <select className="form-control"  onChange={this.onChange}  name="classId" >
                  { classObject.map((item,index)=>{
                    return (
                      <option value={item.classSubjectName}>{item.classSubjectName}</option>
                    )
                      })
                    }
          </select>
        )
      }
      renderCategoryQuestion = () =>{
        let{classObject}=this.state;
        console.log(classObject);
        return ( <select className="form-control"  onChange={this.onChange}  name="examCategoryNumber" >
                  { classObject.map((item,index)=>{
                    return (
                      <option value={item.classSubjectName}>{item.classSubjectName}</option>
                    )
                      })
                    }
          </select>
        )
      }
      onChange(e) {
        this.setState({ [e.target.name]: e.target.value })//cp nht gi tr input
      }
      onSubmit(e) {
        var r = this;
        e.preventDefault();
        const { examName, examEasyNumber, examMediumNumber,examDifficultNumber,examCategoryNumber,examTimeMake,classId } = this.state;
        let formData={
        } ;
        formData.examDifficultNumber=examDifficultNumber;
        formData.examMediumNumber= examMediumNumber;
        formData.examName= examName;
        formData.examEasyNumber=examEasyNumber;
        formData.examCategoryNumber=examCategoryNumber;
        formData.examTimeMake=examTimeMake;
        formData.classId=classId;
        const {examActionsCreators} = this.props;
        const { addexam } = examActionsCreators;
        addexam(formData);
        toastSuccess('Thm bi kim tra thnh cng');
        setTimeout(()=>{
          r.props.history.push('/admin/exam');
        },500)
      }
    render() {
        return (
            <div>
             <section className="content">
                 <div className="box box-info">
        <div className="box-header with-border">
          <h3 className="box-title">Thm  thi</h3>
        </div>
        {/* /.box-header */}
        {/* form start */}
        <form className="form-horizontal" noValidate onSubmit={this.onSubmit}>
          <div className="box-body">
            <div className="form-group">
              <label style={{textAlign: 'left'}} htmlFor="inputEmail3" className="col-sm-2 control-label">Tn  thi</label>
              <div className="col-sm-10" style={{marginLeft: '-5%'}} >
                <input type="hidden" className="form-control"  placeholder="text" onChange={this.onChange} name="_id" value={this.state._id}/>
                
                <input type="text" className="form-control"  placeholder="Tn  thi" onChange={this.onChange} name="examName" value={this.state.examName}/>
              </div>
            </div>
            <div className="form-group">
              <label style={{textAlign: 'left'}} htmlFor="inputEmail3" className="col-sm-2 control-label">Loi cu hi</label>
              <div className="col-sm-10" style={{marginLeft: '-5%'}}>
              {this.renderCategoryQuestion()}
                {/* <input type="text" className="form-control"  placeholder="S cu trung bnh" onChange={this.onChange} name="examMediumNumber" value={this.state.examMediumNumber}/> */}
              </div>
            </div>
            <div className="form-group">
              <label style={{textAlign: 'left'}} htmlFor="inputEmail3" className="col-sm-2 control-label">Thi gian lm bi</label>
              <div className="col-sm-10" style={{marginLeft: '-5%'}}>
              <select className="form-control"  onChange={this.onChange}  name="examTimeMake" >
                  <option value="05 pht">05 pht</option>
                  <option value="10 pht">10 pht</option>
                  <option value="15 pht">20 pht</option>
              </select>
                {/* <input type="text" className="form-control"  placeholder="Thi gian lm bi" onChange={this.onChange} name="examTimeMake" value={this.state.examTimeMake}/> */}
              </div>
            </div>
            <div className="form-group">
              <label style={{textAlign: 'left'}} htmlFor="inputPassword3" className="col-sm-2 control-label">Lp lm bi</label>
              <div className="col-sm-10" style={{marginLeft: '-5%'}}>
              {this.renderClass()}
                {/* <input type="text" className="form-control" id="inputPassword3" placeholder="Lp lm bi"onChange={this.onChange} name="classId" value={this.state.classId}/> */}
              </div>
            </div>
          </div>
          {/* /.box-body */}
          <div className="box-footer" style={{paddingRight: '69px'}}>
            
            <button type="submit" className="btn btn-info pull-right">Thm</button>
          </div>
          {/* /.box-footer */}
        </form>
      </div>
      </section>
     
            </div>

        )
    }
}
const mapStateToProps = state =>{
  return {}
}
const mapDispatchToProps = dispatch =>{
  return {
    examActionsCreators:bindActionCreators(examAction, dispatch)
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(AdminAddItemExam)
